body {
  position: relative;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.56;
  min-width: 320px;
  color: #000;
  background-color: #fff;
}

.title {
  font-size: 38px;
  color: #0074e4;
  text-align: center;
  font-weight: 400;
}

.title_lg {
  font-size: 45px;
  line-height: 1.11;
}

.title_xs {
  font-size: 20px;
  line-height: 2.5;
  letter-spacing: -0.8px;
}

.title_white {
  color: #fff;
}

.title_dark {
  color: #171c34;
}

.view-button {
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 14px;
  text-transform: uppercase;
}

.view-button .icon {
  margin-left: 15px;
  font-size: 9px;
}

.view-button_blue {
  color: #0074e4;
}

.inner {
  max-width: 1026px;
  padding: 0 15px;
  margin: 0 auto;
}

.inner_sm {
  max-width: 830px;
}

.inner_lg {
  max-width: 1074px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  color: #fff;
  padding-top: 21px;
  font-size: 14px;
  z-index: 5;
}

.header__inner {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__left {
  display: flex;
}

.header__menu {
  display: flex;
  align-items: center;
  margin-left: 57px;
}

.logo {
  font-size: 39px;
}

.logo .icon {
  display: block;
}

.main-menu {
  display: flex;
}

.main-menu__item:not(:first-child) {
  margin-left: 40px;
}

.language {
  position: relative;
  text-transform: uppercase;
  padding-right: 17px;
  cursor: pointer;
}

.language__current {
  display: flex;
  align-items: center;
}

.language__current .icon {
  margin-right: 6px;
}

.language__arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  font-size: 4px;
}

.language__arrow .icon {
  display: block;
}

.language__list {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.language:hover .language__list {
  opacity: 1;
  visibility: visible;
}

.language__item {
  padding: 0 17px 0 19px;
}

.language__link {
  position: relative;
  display: inline-block;
  border-bottom: 1px solid transparent;
  transition: border-color .3s;
}

.language__link:before {
  content: "";
  position: absolute;
  top: -3px;
  bottom: -3px;
  left: -10px;
  right: -10px;
}

.language__link:hover {
  border-color: currentColor;
}

.main {
  position: relative;
  background-image: linear-gradient(to bottom, #20232d, #151720);
  overflow: hidden;
  color: #fff;
}

.main:after {
  content: "";
  position: absolute;
  width: 200%;
  height: 400px;
  left: 0;
  top: 100%;
  background-color: #fff;
  transform-origin: 0 0;
  transform: rotate(-5deg);
}

.main__inner {
  position: relative;
  z-index: 2;
}

.main__content {
  max-width: 418px;
  padding-right: 10px;
  position: relative;
}

.main__content-icon_blurred {
  filter: blur(10.2px);
}

.main__wrapper {
  padding: 100px 0 50px;
  height: 100vh;
  min-height: 700px;
  display: flex;
  align-items: center;
}

.main__left, .main__right {
  width: 50%;
}

.main__right {
  align-self: flex-end;
}

.main__img {
  width: 960px;
  margin-left: -92px;
}

.main__img img {
  max-width: 100%;
  display: block;
}

.main__title {
  margin: 7px 0 25px;
  text-align: left;
}

.main__subtitle {
  text-align: left;
}

.svg-triangle {
  position: absolute;
  font-size: 230px;
  color: rgba(0, 116, 228, 0.22);
}

.svg-triangle .icon {
  display: block;
}

.benefits {
  padding-top: 80px;
}

.benefits__subtitle {
  max-width: 520px;
  margin: 24px auto 0;
  text-align: center;
}

.benefits__content {
  margin-top: 119px;
  display: flex;
  flex-wrap: wrap;
}

.benefits__item {
  width: 50%;
}

.benefits-item {
  padding: 40px 30px 99px;
  text-align: center;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  transition: box-shadow .3s;
}

.benefits-item:hover {
  box-shadow: 0 4px 15px 0 rgba(7, 42, 68, 0.1);
}

.benefits-item__icon {
  flex: 1 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.benefits-item__icon img {
  display: block;
  max-width: 100%;
}

.benefits-item__title {
  flex: 0 0 auto;
  margin-top: 24px;
  font-size: 24px;
  color: #171c34;
}

.benefits-item__text {
  flex: 0 0 auto;
  margin-top: 40px;
  color: #4a4a4a;
}

[data-tabs] [data-tabs-control] {
  cursor: pointer;
  user-select: none;
}

[data-tabs] [data-tabs-control][disabled], [data-tabs] [data-tabs-control].is-disabled {
  cursor: not-allowed;
  user-select: none;
}

[data-tabs] [data-tabs-wrapper] {
  position: relative;
}

[data-tabs] [data-tabs-container] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

[data-tabs] [data-tabs-container].is-open {
  position: relative;
  opacity: 1;
  visibility: visible;
}

.tabs {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.tabs__out {
  width: 35%;
}

.tabs__wrapper {
  width: 65%;
}

.tabs__container {
  padding-left: 153px;
}

.tabs__item {
  padding: 30px;
  display: flex;
  align-items: center;
  transition: color .3s, box-shadow .3s;
  border-radius: 7px;
  font-size: 24px;
  color: #89969f;
  line-height: 1;
  min-height: 99px;
}

.tabs__item.is-active {
  color: #0074e4;
  box-shadow: 0 0 20px 0 rgba(29, 17, 86, 0.12);
}

.tabs__icon {
  margin-right: 27px;
  transition: margin .3s;
}

.tabs__item.is-active .tabs__icon {
  margin-right: 35px;
}

.tabs__icon .icon {
  display: block;
}

.tabs__icon .icon-html {
  font-size: 19px;
}

.tabs__icon .icon-css, .tabs__icon .icon-javascript {
  font-size: 28px;
}

.tabs__icon .icon-animation {
  font-size: 22px;
}

.tabs__icon .icon-resources {
  font-size: 33px;
}

.tabs__icon .icon-github {
  font-size: 36px;
}

.tabs__images {
  margin-top: 30px;
  display: flex;
}

.tabs__img {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tabs__img:not(:first-child) {
  margin-left: 24px;
}

.tabs__img img {
  display: block;
  max-width: 100%;
}

.tabs__text {
  max-width: 487px;
}

.how-work__tabs {
  margin-top: 65px;
}

.portfolio {
  padding-top: 124px;
}

.portfolio__top {
  margin: 40px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.portfolio__controls {
  font-size: 14px;
  text-transform: uppercase;
  display: flex;
}

.portfolio__control {
  position: relative;
  cursor: pointer;
  user-select: none;
}

.portfolio__control:not(:first-child) {
  margin-left: 30px;
}

.portfolio__control:before {
  content: "";
  position: absolute;
  left: 50%;
  width: 115%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 1px;
  opacity: 0;
  visibility: hidden;
  background-color: #0074e4;
  transition: opacity .3s, visibility .3s;
}

.portfolio__control.is-active:before {
  opacity: 1;
  visibility: visible;
}

.portfolio__list-wrap {
  margin: 0 -12px;
}

.portfolio-item {
  width: 50%;
  padding: 12px;
}

.portfolio-item__wrap {
  position: relative;
  transition: box-shadow .3s;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.15);
}

.portfolio-item__wrap:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(to left, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.86) 73%, #ffffff);
}

.no-touch .portfolio-item__wrap:hover {
  box-shadow: 0 11px 33px 0 rgba(7, 42, 68, 0.3);
}

.portfolio-item__title, .portfolio-item__link {
  position: absolute;
  z-index: 1;
}

.portfolio-item__img {
  padding: 10px;
}

.portfolio-item__img img {
  display: block;
  max-width: 100%;
}

.portfolio-item__title {
  font-size: 24px;
  color: #0074e4;
  font-weight: 400;
  left: 32px;
  top: 35px;
  max-width: 250px;
}

.portfolio-item__link {
  left: 32px;
  bottom: 0;
}
